html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-roboto);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --background: #ebf6f8;
  --orange: #ff5c00;
  --grey-borders: #ebebeb;
  --text: #000000;
  --white: #ffffff;
  --grey-text: #727272;
}

:root {
  --font-open-sans: "Open Sans", sans-serif;
  --font-roboto: "Roboto", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  max-width: 68.75rem;
}
@media (min-width: 64em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  margin-top: 2.1875rem;
}
@media (min-width: 64em) {
  .header {
    display: flex;
    justify-content: space-between;
    position: absolute;
    top: 0;
    width: 100%;
  }
}
.header__logo {
  width: 30%;
  margin-left: 1.875rem;
}
@media (min-width: 40em) {
  .header__logo {
    width: 14%;
  }
}
@media (min-width: 64em) {
  .header__logo {
    width: 7%;
    margin: 0 3.75rem;
  }
}
.header__bag {
  background: url("../images/svg/bag.svg");
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  width: 7%;
  margin-right: 1.875rem;
  cursor: pointer;
}
@media (min-width: 64em) {
  .header__bag {
    width: 4%;
  }
}
.header__bag--count {
  position: relative;
  font-size: 0.75rem;
  top: 0.5625rem;
  left: 0.5625rem;
  font-family: var(--font-open-sans);
  font-weight: 600;
  text-decoration: none;
}
@media (min-width: 40em) {
  .header__bag--count {
    left: 0.4375rem;
    top: 0.4375rem;
  }
}
@media (min-width: 64em) {
  .header__bag--count {
    font-size: 0.875rem;
    top: 0.75rem;
    left: 0.625rem;
  }
}

main {
  display: flex;
  flex-direction: column;
}
@media (min-width: 64em) {
  main {
    display: flex;
    flex-direction: row;
  }
}

.landing {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin-top: 2.5rem;
}
@media (min-width: 64em) {
  .landing {
    width: 50%;
    height: 100vh;
    justify-content: center;
    align-items: center;
    margin-top: 0;
  }
}
.landing__title {
  font-size: 1.875rem;
  font-weight: 500;
  margin-bottom: 1.875rem;
}
@media (min-width: 64em) {
  .landing__title {
    font-size: 3.25rem;
  }
}
.landing__description {
  color: var(--grey-text);
  max-width: 22.8125rem;
  font-size: 1rem;
  line-height: 1.5;
  margin: auto;
  margin-bottom: 5rem;
}
@media (min-width: 64em) {
  .landing__description {
    margin: 0 auto;
    max-width: 30rem;
    font-size: 1.125rem;
  }
}

.card {
  display: flex;
  flex-direction: column;
  background-color: var(--white);
  width: 80%;
  height: auto;
  margin: 5rem 1.25rem;
  padding: 1.25rem;
  text-align: center;
  border-radius: 0.25rem;
  box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.15);
  transition: 0.3s;
}
.card:hover {
  box-shadow: 0 16px 32px 0 rgba(0, 0, 0, 0.15);
}
@media (min-width: 40em) {
  .card {
    width: 40%;
  }
}
@media (min-width: 64em) {
  .card {
    width: 40%;
    padding-bottom: 3.125rem;
    height: auto;
  }
}
.card__container {
  background: var(--background);
  width: 100%;
  display: flex;
  justify-content: center;
}
@media (min-width: 64em) {
  .card__container {
    width: 50%;
    height: 100vh;
    justify-content: center;
    align-items: center;
  }
}
.card__image {
  width: 100%;
}
.card__title {
  font-size: 1.125rem;
  margin: 0.875rem 0;
}
@media (min-width: 64em) {
  .card__title {
    font-size: 1.375rem;
    line-height: 1.6;
  }
}
.card__price {
  color: var(--orange);
  font-weight: 800;
  font-size: 1rem;
  font-family: var(--font-roboto);
  margin: 0.625rem 0;
  margin-bottom: 1.375rem;
  letter-spacing: 0.125rem;
}
@media (min-width: 64em) {
  .card__price {
    font-size: 1.125rem;
    margin-top: 0.5rem;
  }
}
.card__cta {
  background-color: var(--orange);
  color: var(--white);
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 0.125rem;
  font-family: var(--font-roboto);
  font-size: 0.8125rem;
  border-radius: 1.5rem;
  padding: 0.875rem;
  width: 60%;
  margin: auto;
  margin-bottom: 2rem;
  cursor: pointer;
}
@media (min-width: 64em) {
  .card__cta {
    margin: 0.75rem auto;
    font-size: 0.9375rem;
    padding: 1.125rem;
    border-radius: 2rem;
  }
}

.overlay {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.3);
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.8s;
}

.cart {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  width: 0%;
  overflow-x: hidden;
  height: 100%;
  background-color: var(--white);
  transition: 0.8s;
}
@media (max-width: 63.9375em) {
  .cart {
    max-width: 90%;
  }
}
@media (min-width: 64em) {
  .cart {
    max-width: 25%;
    transition: 0.8s;
  }
}
.cart__top {
  display: flex;
  flex-direction: row;
  position: fixed;
  top: 0;
  width: 0%;
  right: 0;
  justify-content: space-between;
  padding: 0.5em;
  border-bottom: solid 2px var(--grey-borders);
  background-color: var(--white);
  transition: 0.8s;
  opacity: 0;
}
@media (min-width: 64em) {
  .cart__top {
    max-width: 25%;
  }
}
@media (max-width: 63.9375em) {
  .cart__top {
    max-width: 90%;
  }
}
.cart__top h2 {
  font-weight: 500;
  margin-left: 1rem;
}
.cart__bottom {
  display: flex;
  flex-direction: column;
  position: fixed;
  bottom: 0;
  right: 0;
  text-align: center;
  margin: auto;
  justify-content: space-between;
  border-top: solid 1px var(--grey-borders);
  width: 0%;
  background-color: var(--white);
  transition: 0.8s;
}
@media (min-width: 64em) {
  .cart__bottom {
    max-width: 25%;
    opacity: 0;
  }
}
@media (max-width: 63.9375em) {
  .cart__bottom {
    max-width: 90%;
    opacity: 0;
  }
}
.cart__close {
  background: url("../images/svg/close.svg");
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  width: 4%;
  margin-top: 1.75rem;
  margin-right: 1.375rem;
  cursor: pointer;
}
.cart__subtotal {
  font-size: 1.125rem;
  margin: 1.25rem 0;
}
.cart__checkout {
  background-color: var(--orange);
  color: var(--white);
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 0.125rem;
  font-family: var(--font-roboto);
  font-size: 0.8125rem;
  border-radius: 1.5rem;
  padding: 0.875rem;
  width: 80%;
  margin: auto;
  margin-bottom: 1.375rem;
}
.cart__continue {
  text-transform: uppercase;
  text-decoration: underline;
  text-underline-offset: 0.375rem;
  width: 65%;
  padding: 0.125rem;
  padding-bottom: 0.375rem;
  font-size: 0.875rem;
  font-weight: 600;
  letter-spacing: 0.125rem;
  margin: auto;
  font-family: var(--font-open-sans);
  margin-bottom: 2.5rem;
  cursor: pointer;
}
.cart__products {
  height: 66%;
  width: 100%;
  margin-top: 23%;
  overflow-y: scroll;
}
@media (min-width: 40em) {
  .cart__products {
    margin-top: 13%;
    height: 72%;
  }
}
@media (min-width: 64em) {
  .cart__products {
    margin-top: 18%;
    height: 70%;
    width: 100%;
  }
}

.product {
  display: flex;
  flex-direction: row;
  border-bottom: solid 1px var(--grey-borders);
  width: 85%;
  margin: 1rem auto;
}
.product__image {
  width: 42%;
  padding: 0.25rem;
}
.product__descripition {
  display: flex;
  flex-direction: column;
  text-align: left;
  max-width: 10.625rem;
  padding: 1rem;
  margin-top: 0.75rem;
}
.product__title {
  line-height: 1.4;
}
@media (min-width: 40em) {
  .product__title {
    font-size: 1.375rem;
    margin-top: 1.625rem;
  }
}
@media (min-width: 64em) {
  .product__title {
    margin-top: 0.875rem;
    font-size: 1.125rem;
    max-width: 11.25rem;
  }
}
.product__price {
  color: var(--orange);
  font-weight: 600;
}
@media (min-width: 40em) {
  .product__price {
    font-size: 1.375rem;
  }
}
@media (min-width: 64em) {
  .product__price {
    font-size: 1.125rem;
  }
}
.product__remove {
  text-transform: uppercase;
  letter-spacing: 0.0625rem;
  font-size: 0.625rem;
  margin-top: 1.125rem;
  margin-bottom: 1rem;
  padding-bottom: 0.1875rem;
  width: 3.125rem;
  color: rgba(0, 0, 0, 0.4);
  text-decoration: underline rgba(0, 0, 0, 0.3);
  text-underline-offset: 0.1875rem;
}
@media (min-width: 40em) {
  .product__remove {
    font-size: 0.8125rem;
  }
}
@media (min-width: 64em) {
  .product__remove {
    font-size: 0.625rem;
  }
}